import { App, Notice, Plugin, PluginSettingTab, Setting } from "obsidian";

interface DevScaffoldSettings {
  debug: boolean;
  greeting: string;
}

const DEFAULT_SETTINGS: DevScaffoldSettings = {
  debug: true,
  greeting: "Hello from the bundled main.js!",
};

export default class DevScaffoldPlugin extends Plugin {
  settings: DevScaffoldSettings;

  async onload() {
    await this.loadSettings();

    this.addCommand({
      id: "dev-scaffold-say-hello",
      name: "Say hello",
      callback: () => {
        new Notice(this.settings.greeting);
        if (this.settings.debug) {
          console.debug("[dev-scaffold] Say hello triggered", { greeting: this.settings.greeting });
        }
      },
    });

    this.addSettingTab(new DevScaffoldSettingTab(this.app, this));
  }

  onunload() {
    // Cleanup if needed
    if (this.settings.debug) console.debug("[dev-scaffold] Plugin unloaded");
  }

  async loadSettings() {
    this.settings = Object.assign({}, DEFAULT_SETTINGS, await this.loadData());
    if (this.settings.debug) console.debug("[dev-scaffold] Settings loaded", this.settings);
  }

  async saveSettings() {
    await this.saveData(this.settings);
    if (this.settings.debug) console.debug("[dev-scaffold] Settings saved", this.settings);
  }
}

class DevScaffoldSettingTab extends PluginSettingTab {
  plugin: DevScaffoldPlugin;

  constructor(app: App, plugin: DevScaffoldPlugin) {
    super(app, plugin);
    this.plugin = plugin;
  }

  display(): void {
    const { containerEl } = this;
    containerEl.empty();

    containerEl.createEl("h2", { text: "Dev Scaffold Settings" });

    new Setting(containerEl)
      .setName("Debug logging")
      .setDesc("Write debug logs to the developer console.")
      .addToggle((toggle) =>
        toggle
          .setValue(this.plugin.settings.debug)
          .onChange(async (value) => {
            this.plugin.settings.debug = value;
            await this.plugin.saveSettings();
          }),
      );

    new Setting(containerEl)
      .setName("Greeting")
      .setDesc("Text shown when you run the command.")
      .addText((text) =>
        text
          .setPlaceholder("Hello...")
          .setValue(this.plugin.settings.greeting)
          .onChange(async (value) => {
            this.plugin.settings.greeting = value;
            await this.plugin.saveSettings();
          }),
      );
  }
}
